.contacts {
  max-width: 1240px;
  width: 100%;
  margin: auto;
  padding: 5rem 0;
  position: relative;
  .heading {
    text-align: center;
    padding: 1rem 0;

    h2 {
      font-size: 2.125rem;
      color: $primary_white;
    }

    .underline {
      position: relative;
      display: inline-block;

      img {
        position: absolute;
        top: -1.75rem;
        left: -1rem;
      }
    }
    h3 {
      font-size: 1.45rem;
      padding: 1rem 0;
      color: $primary_white;
      font-weight: 400;
    }
    p {
      font-size: 1.25rem;
      color: gray;
      font-weight: 400;
      padding: 0.5rem 0 1rem;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    .contact_form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      row-gap: 1rem;
      width: 70%;
      margin: auto;
      padding: 1rem 0;
      .input_elements {
        white-space: nowrap;
        display: flex;
        flex-direction: row;
        column-gap: 2rem;
        width: 100%;
        input {
          width: 100%;
          padding: 0.65rem 1rem;
          background-color: transparent;
          border: 1px solid;
          border-image-slice: 1;
          border-image-source: linear-gradient(
            150.61deg,
            #eb474d 2.6%,
            #f143c1 34.77%,
            #6eb393 70.47%,
            #1bb8dd 99.13%
          );
          background: linear-gradient(
            180deg,
            rgba(255, 255, 255, 0.08) 0%,
            rgba(255, 255, 255, 0.04) 100%
          );
          box-shadow: 0px 2.77507px 16.6504px -0.693767px rgba(0, 0, 0, 0.2);
          backdrop-filter: blur(27.7507px);
          font-size: 1rem;
          font-weight: 300;
          letter-spacing: 0.035rem;
          color: $primary_white;
          &:focus {
            outline: none;
            color: $primary_white;
          }
        }
      }
      .textarea_element {
        display: flex;
        align-items: center;
        textarea {
          width: 100%;
          margin: auto;
          padding: 0.65rem 1rem;
          background-color: transparent;
          border: 1px solid;
          border-image-slice: 1;
          border-image-source: linear-gradient(
            150.61deg,
            #eb474d 2.6%,
            #f143c1 34.77%,
            #6eb393 70.47%,
            #1bb8dd 99.13%
          );
          background: linear-gradient(
            180deg,
            rgba(255, 255, 255, 0.08) 0%,
            rgba(255, 255, 255, 0.04) 100%
          );
          box-shadow: 0px 2.77507px 16.6504px -0.693767px rgba(0, 0, 0, 0.2);
          backdrop-filter: blur(27.7507px);
          font-size: 1rem;
          font-weight: 300;
          letter-spacing: 0.035rem;
          color: $primary_white;
          &:focus {
            outline: none;
            color: $primary_white;
          }
        }
      }
      button {
        @include primaryBtn(1.025rem);
        display: block;
        &:hover {
          color: $bg_color;
        }
      }
    }
    .contact_social {
      padding: 2rem 0 2.25rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      h3 {
        color: $primary_white;
        padding: 2rem 0;
        text-align: center;
        font-weight: 400;
      }
      .social_icons {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 2rem;
        .socialicon {
          transition: 0.3s all ease-in;
          &:hover {
            filter: invert(81%) sepia(11%) saturate(1778%) hue-rotate(117deg)
              brightness(92%) contrast(88%);
          }
        }
      }
    }
  }
  .bg_img {
    &:after {
      position: absolute;
      content: "";
      width: 50px;
      height: 50px;
      background: #eb474d;
      box-shadow: -14.2632px 0px 21.3947px rgba(255, 255, 255, 0.1),
        14.2632px 0px 18.5421px rgba(255, 255, 255, 0.1),
        inset -14.2632px 4.27895px 15.6895px rgba(255, 143, 148, 0.38),
        inset 14.2632px 4.27895px 15.6895px rgba(254, 104, 109, 0.4);
      border-radius: 50%;
      top: 18rem;
      right: 10rem;
      z-index: -1;
    }
    &:before {
      position: absolute;
      content: "";
      width: 80px;
      height: 80px;
      background: #5ad3be;
      box-shadow: -13.0526px 0px 19.5789px rgba(255, 255, 255, 0.1),
        13.0526px 0px 16.9684px rgba(255, 255, 255, 0.1),
        inset -13.0526px 3.91579px 14.3579px #18bb9f,
        inset 13.0526px 3.91579px 14.3579px #02a78a;
      border-radius: 50%;
      bottom: 18rem;
      left: 8rem;
      z-index: -1;
    }
  }
}

@media screen and (max-width: 470px) {
  .input_elements {
    flex-direction: column !important;
    row-gap: 1.5rem;
  }
  .bg_img {
    &:before {
      display: none;
    }
  }
}

@media screen and (max-width: 940px) {
  .input_elements {
    flex-direction: column !important;
    row-gap: 1.5rem;
  }
  .bg_img {
    &:after {
      width: 50px;
      height: 50px;
      top: 18rem;
      right: 10rem;
    }
    &:before {
      width: 80px;
      height: 80px;
      bottom: 18rem;
      left: 8rem;
    }
  }
}
